
































import Vue from "vue";
import ShowPage from "@/components/ShowPage.vue";
import axios from "axios";
const log = window.console.log;

interface Topic {
  type: number;
  info: string;
  solutions: number[];
  options: string[];
}

const param = new URLSearchParams(location.search);
const vm = Vue.extend({
  components: {},
  data() {
    return {
      topics: [],
      name: "",
      url: "#"
    };
  },
  async mounted() {
    const closePage = () => {
      location.href = "https://gitee.com/lisonge/scu-sports-data";
    };
    if (param.has("id") && param.has("sn")) {
      const id = param.get("id"),
        sn = param.get("sn");
      const url = `https://lisonge.gitee.io/scu-sports-data/json/${id}/${sn}.json`;
      try {
        const response = await axios.get(url);
        const { data } = response;
        this.name = data["course"]["name"];
        this.url = url;
        const topics: Topic[] = data["topics"];
        topics.forEach(el => {
          Object.freeze(el);
          // @ts-ignore
          this.topics.push(el);
        });
      } catch (error) {
        closePage();
      }
    } else {
      closePage();
    }
  }
});

export default vm;
